.section-review-project {
    background-color: var(--color-bg-02);
    padding: 50px 0;

    @media screen and (min-width: $w1440) {
        padding: 95px 0 95px 0;
    }

    &--min {

        & .review-project {
            max-width: 900px;

            @media screen and (min-width: $w1376) {
                max-width: 1050px;
            }
        }


    }
}

.review-project {
    background-color: var(--color-bg-02);

    &.animate {
        & .review-project {
            &__title {
                opacity: 0;
                transition: 0.5s ease;
                transition-delay: 0.3s;
                transform: translateY(30px);
            }


            &__thumb {
                opacity: 0;
                transition: 0.5s ease;
                transform: translateY(30px);
            }

            &__slider {
                & .splide__slide {
                    opacity: 0;
                    transform: translateX(400px);
                    transition: 1s ease;
                }
            }
        }

        &.animated {
            & .review-project {
                &__title {
                    opacity: 1;
                    transform: translateY(0);
                }

                &__thumb {
                    opacity: 1;
                    transition-delay: 0.8s;
                    transform: translateY(0);
                }

                &__slider {
                    & .splide__slide {
                        opacity: 1;
                        transform: translateX(0);

                        &:nth-child(1) {
                            transition-delay: 0.1s;
                        }

                        &:nth-child(2) {
                            transition-delay: 0.4s;
                        }
                    }
                }

            }

        }
    }


    &__title {
        @include font-h1;
        padding-bottom: 0.8em;
    }

    &__slider {
        padding-bottom: 30px;
    }

    &__thumb {
        padding: 4px;
        background-color: var(--color-bg-01);

        @media screen and (min-width: $w576) {
            padding: 16px;
        }

        & .splide__slide.is-active {
            border-color: transparent !important;
        }



        @media screen and (max-width: $w576) {
            & .project-thumb {

                &__next,
                &__prev {
                    display: none !important;
                }

                &__slider {
                    width: 100%;
                }
            }
        }

    }
}

.card-review {
    padding: 16px;
    background-color: var(--color-bg-01);
    height: 100%;
    display: flex;
    flex-direction: column;

    @media screen and (min-width: $w1200) {
        padding: 24px;
    }

    &__image {

        padding-bottom: 16px;

        & picture {
            @include picture(56%, cover);
        }
    }

    &__name {
        padding-bottom: 10px;
        color: var(--color-text-02);
        font-size: 14px;
    }

    &__text {
        padding-bottom: 16px;
        color: var(--color-text-01);
        font-weight: 700;
        line-height: 155%;

        @media screen and (min-width: $w1440) {
            font-size: 18px;
            padding-bottom: 40px;
        }
    }

    &__bottom {
        display: flex;
        justify-content: space-between;
        margin-top: auto;
    }

    &__autor,
    &__project {
        display: flex;
        flex-direction: column;

        font-size: 13px;

        @media screen and (min-width: $w760) {
            font-size: 14px;
        }

        & span {
            color: var(--color-text-02);
        }

        & strong {
            color: var(--color-text-01);
            font-weight: 700;

            @include line-clamp(2);
        }
    }

    &__project {

        padding-right: 24px;

        & strong {
            padding-right: 16px;
        }


    }

    &__autor {
        & strong {
            white-space: nowrap;
            text-align: right;
        }

        & span {
            text-align: right;
        }
    }
}